export default {
  props: {
    block: Object,
  },
  data(){
    return {
      subtitle: '',
      url:''
    }
  },
  computed: {
    f_subtitle() {
        return this.block.data.subtitle[0].text
    },
    f_url(){
      return this.block.data.main.url
    }
  }
};
